// Question.js
import React from 'react';
import { Button, Form, Stack } from 'react-bootstrap';

function Question({ question, handleAnswerOptionClick, previousQuestion, nextQuestion, isFirst, isLast, showResults }) {
  return (
    <div>
      <p style={{ whiteSpace: 'pre-line' }}>{question.question}</p>
      <Stack gap={2}>
        {question.options.map((option) => (
          <span key={option}>
            <Form.Check // prettier-ignore
                type="radio"
                label={option}
                name="option"
                value={option}
                onClick={() => handleAnswerOptionClick(option)}
                defaultChecked= {(question.selectedAnswer === option)}
            />
          </span>
        ))}
      </Stack>
      <div style={{ textAlign: 'end', fontStyle: 'italic', fontSize: 'smaller'}}>source: {question.chapter + ' - ' + question.questionNumber}</div>

      <div style={{'margin-top': '20px'}}>
        <Stack direction='horizontal' gap={3}>
          <Button variant="secondary" onClick={previousQuestion} disabled={isFirst}>Previous</Button>
          <Button variant="primary" onClick={nextQuestion} disabled={isLast}>Next</Button>
          <Button variant="success" className='ms-auto' onClick={showResults}>Submit Answers</Button>
        </Stack>
      </div>
    </div>
  );
}

export default Question;
