// Result.js
import React from 'react';
import { ListGroup } from 'react-bootstrap';

function Result({ quizData }) {
  return (
    <div>
      <h2>Results</h2>
      <ListGroup as="ol" numbered>
        {
            quizData.map((question) => (
              <ListGroup.Item
                as="li"
                className="d-flex justify-content-between align-items-start">
                <div className="ms-2 me-auto">
                  <div className="fw-bold">{question.question}</div>
                  <div style={{ color: (question.selectedAnswer === question.answer)? 'green': 'red'}}>Your Answer: {question.selectedAnswer}</div>
                  <div>Correct Answer: {question.answer}</div>
                </div>
              </ListGroup.Item>
            ))
        }
      </ListGroup>
    </div>
  );
}

export default Result;
