import React, { useState, useEffect } from 'react';
import './App.css';
import Quiz from './Quiz';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button, Col, Form, Row } from 'react-bootstrap';

const quizQuestions = {}

function App() {
  const [quizStarted, setQuizStarted] = useState(false);
  const [timeLimit, setTimeLimit] = useState(30);
  const [endTime, setEndTime] = useState(null);
  const [questionsCount, setQuestionsCount] = useState(100);
  const [quizData, setQuizData] = useState(null)
  const [chapters, setChapters] = useState({})
  


  const startQuiz = () => {
    console.log(quizQuestions)
    setEndTime(Date.parse(new Date()) + (timeLimit * 60 * 1000))
    const shuffledQuestions = [...Object.values(quizQuestions).flat()].sort(() => 0.5 - Math.random());  // Shuffle the array
    const selectedQuestions = shuffledQuestions.slice(0, questionsCount);
    //console.log(selectedQuestions)
    setQuizData(selectedQuestions)
    setQuizStarted(true)
  }

  const fetchChapterQuestions = (ch, selected) => {
      if (selected) {
        //console.log(ch)
        fetch("questions/" + ch + ".csv")
          .then((response) => response.text())
          .then((csvText) => {
            // Parse CSV using PapaParse
            const rows = csvText.split('\n');
            //console.log(rows)
            var lineNo = 0;
            const questions = []
            while(rows.length >= (lineNo + 7)) {
              //console.log(rows[lineNo]);
              questions.push({
                questionNumber: rows[lineNo],
                question: rows[lineNo + 1] + '\n' + rows[lineNo + 2],
                options: rows[lineNo + 3]?.trim().split(',').map(option => option.replaceAll('"', '')).map(option => option.trim()),
                answer: rows[lineNo + 4]?.trim(),
                chapter: rows[lineNo + 5]
              })
              lineNo += 7;
            }
            console.log(questions)
            quizQuestions[ch] = questions
          })
      } else {
        quizQuestions[ch] = []
      }
  }

  useEffect(() => {
    fetch("questions/chapters.json")
      .then((response) => response.json())
      .then((config) => {
        const chapters = {}
        for(let i = config.from; i <= config.to; i++) {
          chapters[config.prefix + '' + i] = false
        }
        setChapters(chapters)
      })
  }, []);

  return (
    <div className="App">
      <h1>Mock Test</h1>
      { quizStarted ? (
        <Quiz 
          endTime={endTime}
          quizData={quizData}
        />
      ) : (
        <Row className="justify-content-md-center">
          <Col lg="6">
          <Form>
            <Form.Group className="mb-6" controlId="timeLimit">
              <Form.Label>Exam time limit (in minutes):</Form.Label>
              <Form.Control type="number"  
                value={timeLimit}
                onChange={e => setTimeLimit(e.target.value)}/>
            </Form.Group>
            <Form.Group className="mb-6" controlId="questionsCount">
              <Form.Label>Number of questions:</Form.Label>
              <Form.Control type="number"  
                value={questionsCount}
                onChange={e => setQuestionsCount(e.target.value)}/>
            </Form.Group>
            <Form.Group className="mb-6" controlId="chapters">
              <Form.Label>Chapters:</Form.Label>
              <div className="chapters_container">
              
                { Object.keys(chapters).map((ch) => (
                  <Form.Check // prettier-ignore
                    type="checkbox"
                    id={`default-${ch}`}
                    label={ch}
                    onChange={(e) => fetchChapterQuestions(ch, e.target.checked)}
                  />
                ))}
              
              </div>
            </Form.Group>
            <div className="d-grid gap-2">
              <Button variant="success" onClick={startQuiz}>Start Exam</Button>
            </div>
          </Form>
          </Col>
        </Row>
      )}
    </div>
  );
}

export default App;
