// Quiz.js
import React, { useState, useRef } from 'react';
import Question from './Question';
import Result from './Result';
import { Alert, Stack } from 'react-bootstrap';

function Quiz({endTime, quizData}) {
  const Ref = useRef(null);
  const [currentQuestion, setCurrentQuestion] = useState(0);
  //const [score, setScore] = useState(0);
  const [showResult, setShowResult] = useState(false);
  const [timeLeft, setTimeLeft] = useState(null);
  //const endTime = Date.parse(startTime) + (timeLimit * 60 * 1000);

  const setTimeRemaining = () => {
    const total = endTime - Date.parse(new Date());
    if (total > 0) {
      const seconds = Math.floor((total / 1000) % 60);
      const minutes = Math.floor(
          (total / 1000 / 60)
      );
      setTimeLeft((minutes < 10? '0' + minutes: minutes) + ':' + (seconds < 10? '0' + seconds: seconds));
    } else {
      setShowResult(true)
    }
  };

  if (Ref.current) clearInterval(Ref.current);
  const id = setInterval(() => {
    setTimeRemaining();
  }, 1000);
  Ref.current = id;

  const handleAnswerOptionClick = (selectedAnswer) => {
    quizData[currentQuestion].selectedAnswer = selectedAnswer
  };

  const nextQuestion = () => {
    const nextQuestion = currentQuestion + 1;
    if (nextQuestion < quizData.length) {
      setCurrentQuestion(nextQuestion);
    } else {
      setShowResult(true);
    }
  }

  const previousQuestion = () => {
    if (currentQuestion > 0) {
        setCurrentQuestion(currentQuestion - 1)
    }
  }

  const showResults = () => {
    setShowResult(true)
  }

  return (
    <div>
      {showResult ? (
        <Result quizData={quizData} />
      ) : (
        <div>
        <Alert variant='primary'>
          <span></span>
          <span></span>
          <Stack direction="horizontal" gap={3}>
            <div className="p-2">Question: {currentQuestion + 1} / {quizData.length}</div>
            <div className="vr ms-auto" />
            <div className="p-2">Time left: {timeLeft}</div>
          </Stack>
        </Alert>
        <Question
          question={quizData[currentQuestion]}
          handleAnswerOptionClick={handleAnswerOptionClick}
          previousQuestion={previousQuestion}
          nextQuestion={nextQuestion}
          isFirst={currentQuestion === 0}
          isLast={currentQuestion === (quizData.length - 1)}
          showResults={showResults}
        />
        </div>
      )}
    </div>
  );
}

export default Quiz;
